/* General Layout */
.owner-container {
  display: flex;
  height: 100vh;
  font-family: Arial, sans-serif;
  background-color: #f4f4f9;
  color: #333;
}

/* Sidebar */
.owner-menu {
  width: 250px;
  background-color: #34495e;
  color: #fff;
  padding: 20px 10px;
  box-shadow: 2px 0 5px rgba(0, 0, 0, 0.1);
}

.owner-title {
  font-size: 20px;
  font-weight: bold;
  margin-bottom: 20px;
  text-align: center;
}

.owner-list {
  list-style: none;
  padding: 0;
  margin: 0;
}

.owner-list li {
  padding: 10px 15px;
  margin: 5px 0;
  cursor: pointer;
  border-radius: 5px;
  transition: background-color 0.3s ease;
}

.owner-list li:hover {
  background-color: #2c3e50;
}

.owner-list .active {
  background-color: #1abc9c;
  color: white;
}

/* Main Content */
.owner-main {
  flex: 1;
  display: flex;
  flex-direction: column;
}

.owner-header {
  background-color: #2c3e50;
  color: white;
  padding: 15px 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.btn-logout {
  background-color: #e74c3c;
  border: none;
  color: white;
  padding: 8px 15px;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.btn-logout:hover {
  background-color: #c0392b;
}

.content {
  padding: 20px;
  overflow-y: auto;
}

/* Dashboard */
.dashboard {
  text-align: center;
  margin-top: 50px;
}

.dashboard h2 {
  font-size: 24px;
  color: #34495e;
}

.dashboard p {
  color: #666;
}