/* General styles */
.full-width {
  width: 100%; /* Ajustado a 100% para que no se desborde el contenedor */
}

.header-well {
  background-color: #f5f5f5;
  padding: 10px; /* Reducido de 20px a 10px para menos espacio interno */
  margin-bottom: 10px; /* Reducido de 20px a 10px para menos espacio entre elementos */
}

.header-well-icon i {
  font-size: 48px;
}

.header-well-text {
  margin-top: 5px; /* Reducido de 20px a 5px para menos espacio superior */
}

.divider-menu-h {
  border-bottom: 2px solid #e0e0e0;
  margin: 10px 0; /* Reducido de 20px a 10px para menos espacio vertical */
}

/* Panel styles */
.panel {
  border-radius: 8px;
  overflow: hidden;
  margin-top: 0; /* Asegúrate de eliminar cualquier margen superior */
}

.panel-title {
  background-color: #c5351b;
  color: white;
  padding: 8px; /* Reducido de 10px a 8px */
  font-size: 24px;
}

.panel-content {
  padding: 15px; /* Reducido de 20px a 15px para menos espacio interno */
  
}

/* Form styles */
.mdl-textfield {
  width: 100%;
}

.mdl-textfield__input {
  padding: 8px; /* Reducido de 10px a 8px */
  border: 1px solid #ccc;
  border-radius: 4px;
  margin-bottom: 8px; /* Reducido de 10px a 8px */
}

.mdl-textfield__label {
  font-size: 16px;
  color: #757575;
}

.mdl-button--fab {
  background-color: #c5351b;
  color: white;
  padding: 5px;
}

/* Form row */
.form-row {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.form-row .mdl-textfield {
  flex: 1;
  margin-right: 10px; /* Reducido de 20px a 10px */
}

.form-row .mdl-textfield:last-child {
  margin-right: 0;
}