/* Estilo general para el contenedor principal de la tabla */
.negocio-table-container {
  max-width: 1200px; /* Define un ancho máximo para evitar que la tabla ocupe toda la pantalla */
  margin: 0 auto; /* Centra el contenedor horizontalmente */
  padding: 0px; /* Agrega espacio interno alrededor del contenido */
  background-color: #f9f9f9; /* Fondo claro para resaltar el contenido */
  border-radius: 8px; /* Bordes redondeados */
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1); /* Sombra para dar profundidad */
}

/* Título del contenedor */
h2 {
  text-align: center; /* Centra el texto del título */
  color: #333; /* Color del texto */
  margin-bottom: 20px; /* Espacio debajo del título */
  font-family: 'Arial', sans-serif; /* Fuente moderna y legible */
}

/* Estilos para la tabla */
.negocio-table {
  width: 100%; /* La tabla ocupa todo el ancho del contenedor */
  border-collapse: collapse; /* Elimina los espacios entre las celdas */
  font-size: 16px; /* Tamaño de texto adecuado */
  font-family: 'uppercase'; /* Fuente profesional */
  background-color: #ffffff; /* Fondo blanco para contraste */
}

/* Encabezados de la tabla */
.negocio-table th {
  background-color: #405a73; /* Fondo azul para los encabezados */
  color: white; /* Texto blanco para contraste */
  text-transform: uppercase; /* Convierte el texto a mayúsculas */
  padding: 10px; /* Espaciado interno */
  border: 1px solid #ddd; /* Bordes suaves entre columnas */
}

/* Filas de la tabla */
.negocio-table td {
  padding: 10px; /* Espaciado interno para celdas */
  text-align: center; /* Centra el texto de las celdas */
  border: 1px solid #b1b1b1; /* Bordes suaves entre columnas */
}

/* Alternancia de colores para las filas */
.negocio-table tbody tr:nth-child(odd) {
  background-color: #f4f4f4; /* Fondo gris claro para las filas impares */
}

/* Efecto al pasar el mouse sobre una fila */
.negocio-table tbody tr:hover {
  background-color: #708aa3; /* Fondo gris más claro al hacer hover */
  transition: background-color 0.3s ease; /* Transición suave */
}

/* Botones de editar */
.btn-editar {
  background-color: #b17c08; /* Verde para resaltar acciones positivas */
  color: white; /* Texto blanco */
  border: none; /* Sin bordes */
  padding: 8px 12px; /* Espaciado interno */
  border-radius: 4px; /* Bordes redondeados */
  cursor: pointer; /* Cambia el cursor a un puntero */
  font-size: 14px; /* Tamaño de fuente adecuado */
  font-family: 'uppercase'; /* Fuente profesional */
  transition: background-color 0.3s ease; /* Transición suave al cambiar el fondo */
}

/* Hover para el botón de editar */
.btn-editar:hover {
  transform: scale(1.15);
  background-color: #b17c08; /* Verde más oscuro al pasar el mouse */
}

/* Botones de eliminar */
.btn-eliminar {
  background-color: #dc3545; /* Rojo para resaltar acciones negativas */
  color: white; /* Texto blanco */
  border: none; /* Sin bordes */
  padding: 8px 12px; /* Espaciado interno */
  border-radius: 4px; /* Bordes redondeados */
  cursor: pointer; /* Cambia el cursor a un puntero */
  font-size: 14px; /* Tamaño de fuente adecuado */
  font-family: 'uppercase'; /* Fuente profesional */
  transition: background-color 0.3s ease; /* Transición suave al cambiar el fondo */
}

/* Hover para el botón de eliminar */
.btn-eliminar:hover {
  transform: scale(1.15);
  background-color: #c82333; /* Rojo más oscuro al pasar el mouse */
}

/* Estilos responsivos para pantallas pequeñas */
@media (max-width: 768px) {
  .negocio-table-container {
    padding: 10px; /* Reduce el espaciado interno en pantallas pequeñas */
  }

  .negocio-table th, .negocio-table td {
    font-size: 14px; /* Reduce el tamaño de fuente para ajustarse a pantallas más pequeñas */
    padding: 8px; /* Reduce el espaciado interno */
  }

  h2 {
    font-family: 'uppercase'; /* Fuente profesional */
    font-size: 25px; /* Reduce el tamaño del título */
  }
}