/* Contenedor principal */
.full-width.header-well {
  background: linear-gradient(to right, #405a73, #304c6a); /* Fondo con degradado azul */
  padding: 20px; /* Espaciado interno */
  color: white; /* Texto blanco */
  border-radius: 10px; /* Bordes redondeados */
  text-align: center; /* Centrar el contenido */
  background-color: #f5f5f5;
  padding: 20px;
  margin-bottom: 20px;
  margin-top: 500px ;
  
}

/* Ícono del encabezado */
.full-width.header-well-icon i {
  font-size: 50px; /* Tamaño grande del ícono */
  margin-bottom: 10px; /* Espaciado debajo del ícono */
}

/* Títulos del panel */
.full-width.panel-title {
  background-color: #416588; /* Fondo azul intenso */
  color: white; /* Texto blanco */
  padding: 15px; /* Espaciado interno */
  font-size: 20px; /* Tamaño del texto */
  font-weight: bold; /* Texto en negrita */
  text-transform: uppercase; /* Texto en mayúsculas */
  border-top-left-radius: 8px; /* Bordes redondeados en la esquina superior izquierda */
  border-top-right-radius: 8px; /* Bordes redondeados en la esquina superior derecha */
}

/* Contenido del panel */
.full-width.panel-content {
  padding: 20px; /* Espaciado interno */
  background-color: #f9f9f9; /* Fondo gris claro */
  border-bottom-left-radius: 8px; /* Bordes redondeados inferiores */
  border-bottom-right-radius: 8px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1); /* Sombra para dar profundidad */
}

/* Campos de texto */
.mdl-textfield__input {
  width: 100%; /* Ocupa todo el ancho disponible */
  padding: 10px; /* Espaciado interno */
  border: 1px solid #ccc; /* Bordes grises claros */
  border-radius: 4px; /* Bordes redondeados */
  margin-bottom: 15px; /* Espaciado inferior entre campos */
  font-size: 16px; /* Tamaño del texto */
  box-sizing: border-box; /* Incluye bordes en el cálculo del ancho */
}

/* Campos de texto en modo lectura */
.mdl-textfield__input[readonly] {
  background-color: #e9ecef; /* Fondo gris claro */
  cursor: not-allowed; /* Cursor de "no permitido" */
}

/* Botón principal */
.mdl-button--raised.mdl-button--colored {
  background-color: #28a745; /* Verde para acciones positivas */
  color: white; /* Texto blanco */
  padding: 10px 20px; /* Espaciado interno */
  border: none; /* Sin bordes */
  border-radius: 5px; /* Bordes redondeados */
  font-size: 16px; /* Tamaño del texto */
  cursor: pointer; /* Cambia el cursor al pasar el mouse */
  transition: background-color 0.3s ease; /* Transición suave al cambiar el fondo */
}

/* Efecto hover para el botón */
.mdl-button--raised.mdl-button--colored:hover {
  background-color: #218838; /* Verde más oscuro al hacer hover */
}

/* Mapa */
/* Contenedor del mapa */
.map-container {
  height: 300px; /* Altura fija del mapa */
  border: 2px solid #ddd; /* Borde alrededor del mapa */
  border-radius: 8px; /* Bordes redondeados */
  margin-bottom: 20px; /* Espaciado solo debajo */
  overflow: hidden; /* Elimina contenido que salga del área */
  position: relative; /* Asegura que el contenedor sea el marco del contenido */
  background-color: transparent; /* Elimina cualquier fondo gris */
}

/* Mapa interno */
.map-container > div {
  height: 100%; /* El mapa ocupa todo el alto del contenedor */
  width: 100%; /* El mapa ocupa todo el ancho del contenedor */
  margin: 0; /* Elimina márgenes innecesarios */
  padding: 0; /* Elimina paddings innecesarios */
  background-color: transparent; /* Asegúrate de que no haya colores de fondo */
}

/* Para corregir posibles estilos del grid */
.mdl-grid {
  margin: 0; /* Elimina márgenes externos */
  padding: 0; /* Elimina paddings externos */
}


/* Contenedor de previsualización de imagen */
.mdl-cell img {
  border-radius: 8px; /* Bordes redondeados */
  max-width: 100%; /* Asegura que la imagen no se desborde del contenedor */
  max-height: 200px; /* Limita la altura máxima */
  object-fit: cover; /* Escala la imagen manteniendo proporciones */
}

/* Inputs de carga de archivo */
input[type="file"] {
  display: block; /* Cambia a bloque para alinearse con los elementos */
  margin: 10px auto; /* Centra el input horizontalmente */
  font-size: 14px; /* Tamaño del texto */
}

/* Diseño responsivo */
@media (max-width: 768px) {
  .full-width.panel-content {
    padding: 15px; /* Reduce el espaciado interno en pantallas pequeñas */
  }

  .mdl-textfield__input {
    font-size: 14px; /* Reduce el tamaño del texto */
    padding: 8px; /* Reduce el espaciado interno */
  }

  .mdl-button--raised.mdl-button--colored {
    font-size: 14px; /* Reduce el tamaño del texto del botón */
    padding: 8px 15px; /* Reduce el espaciado del botón */
  }
}