.inicio-container {
  font-family: Arial, sans-serif;
  text-align: center;
  background-image: url('https://wallpapers.com/images/hd/food-4k-jx0j7rqea6yv9phh.jpg'); 
  padding: 30px;
}


.inicio-header {
  background-color: #76350f; /* Color del encabezado */
  padding: 20px;
  color: rgb(238, 228, 228);
  font-size: 20px;
  border-radius: 10px; /* Bordes redondeados */
  text-decoration: underline; /* Subrayado */


}

.inicio-nav a {
  color: white;
  margin: 0 15px;
  text-decoration: none;

}

.inicio-banner {
  background-image: url('/images/bg5.jpg'); /* Imagen de fondo */
  height: 450px; /* Altura del banner */
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: white;
  background-size: cover;
}

.inicio-button {
  background-color: #76350f; /* Color del botón */
  border-radius: 15px; /* Bordes redondeados */
  border: none;
  padding: 10px 20px;
  color: white;
  cursor: pointer;
  font-size: 17px;
}

.inicio-description {
  background-image: 
    linear-gradient(rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.6)), /* Capa de difuminado más fuerte */
    url('/images/inicio1.jpg'); /* Imagen de fondo */
  background-position: center; /* Centra la imagen de fondo */
  background-size: cover; /* Hace que la imagen cubra todo el contenedor */
  background-repeat: no-repeat; /* Evita que la imagen se repita */
  color: #fff; /* Color de texto blanco */
  margin: 50px auto; /* Centrado y separación superior/inferior */
  padding: 40px; /* Espaciado interno más amplio */
  border-radius: 15px; /* Bordes redondeados */
  width: 80%; /* Ancho del contenedor */
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.15); /* Sombra más profunda */
  text-align: center; /* Centra el texto */
  font-family: 'Arial', sans-serif; /* Tipografía más moderna */
  position: relative; /* Para que el contenido esté sobre la imagen */
}

.inicio-description h3 {
  font-size: 2rem; /* Tamaño de fuente mayor para el título */
  margin-bottom: 20px; /* Separación inferior */
  font-weight: bold; /* Negrita en el título */
  color: #fffae6; /* Color suave para el título */
  position: relative; /* Asegura que el título esté por encima de la imagen */
  z-index: 1; /* Asegura que el título se muestre por encima de la imagen */
}

.inicio-description p {
  line-height: 1.8; /* Mejor legibilidad del párrafo */
  font-size: 1.1rem; /* Tamaño de fuente ligeramente mayor */
  font-style: normal; /* Eliminar cursiva */
  margin: 0 auto;
  max-width: 800px; /* Limita el ancho del texto para mejor lectura */
  position: relative; /* Asegura que el párrafo esté por encima de la imagen */
  z-index: 1; /* Asegura que el párrafo se muestre por encima de la imagen */
}

@media (max-width: 768px) {
  .inicio-description {
    width: 90%; /* Hacer que ocupe un poco más de ancho en pantallas pequeñas */
    padding: 20px; /* Menor espaciado en pantallas pequeñas */
  }

  .inicio-description h3 {
    font-size: 1.5rem; /* Ajustar tamaño del título en pantallas pequeñas */
  }

  .inicio-description p {
    font-size: 1rem; /* Ajuste de fuente para párrafos en pantallas pequeñas */
  }
}





.inicio-footer {
  background-image: 
    linear-gradient(rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.6)), /* Capa de difuminado más fuerte */
    url('/images/inicio1.jpg'); /* Imagen de fondo */

  border-radius: 15px; /* Bordes redondeados */
  color: rgb(216, 94, 37);
  padding: 10px;
}

.social-links a {
  color: rgb(216, 94, 37);
  margin: 0 10px;
}
.inicio-footer {
  background-color: #2c3e50; /* Fondo más elegante */
  color: #ecf0f1; /* Texto claro */
  padding: 20px;
  text-align: center; /* Centra todo el contenido */
  font-family: 'Arial', sans-serif;
}

.inicio-footer p {
  margin: 0;
  font-size: 14px;
}

.inicio-footer .social-links {
  margin-top: 10px; /* Separación entre el texto y los enlaces */
}

.inicio-footer .social-links a {
  color: #f39c12; /* Color de los enlaces */
  margin: 0 10px; /* Espaciado horizontal */
  text-decoration: none;
  font-size: 16px;
  transition: color 0.3s; /* Suavidad en el cambio de color */
}

.inicio-footer .social-links a:hover {
  color: #e74c3c; /* Cambio de color al pasar el cursor */
  text-decoration: underline; /* Subrayado al pasar el cursor */
}

/* Para pantallas pequeñas */
@media (max-width: 600px) {
  .inicio-footer .social-links {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .inicio-footer .social-links a {
    margin: 5px 0; /* Menor espaciado vertical */
  }
}